@-webkit-keyframes tile-flip {
    0% {
        -webkit-transform: rotateX(0deg);
                transform: rotateX(0deg);
    }

    50% {
        -webkit-transform: rotateX(-90deg);
                transform: rotateX(-90deg);
    }

    100% {
        -webkit-transform: rotateX(0deg);
                transform: rotateX(0deg);
    }
}

@keyframes tile-flip {
    0% {
        -webkit-transform: rotateX(0deg);
                transform: rotateX(0deg);
    }

    50% {
        -webkit-transform: rotateX(-90deg);
                transform: rotateX(-90deg);
    }

    100% {
        -webkit-transform: rotateX(0deg);
                transform: rotateX(0deg);
    }
}

.word-tile:nth-child(1) {
    -webkit-animation-delay: 200ms !important;
            animation-delay: 200ms !important;
}

.word-tile:nth-child(2) {
    -webkit-animation-delay: 400ms !important;
            animation-delay: 400ms !important;
}

.word-tile:nth-child(3) {
    -webkit-animation-delay: 600ms !important;
            animation-delay: 600ms !important;
}

.word-tile:nth-child(4) {
    -webkit-animation-delay: 800ms !important;
            animation-delay: 800ms !important;
}

.word-tile:nth-child(5) {
    -webkit-animation-delay: 1000ms !important;
            animation-delay: 1000ms !important;
}

.word-tile.absent {
    background-color: gray;
    border: none;
}

.word-tile.present {
    background-color: rgb(204, 176, 19);
    background-color: var(--present-color);
    border: none;
}

.word-tile.correct {
    background-color: green;
    background-color: var(--correct-color);
    border: none;
}

.word-tile.correct.animation {
    -webkit-animation-fill-mode: forwards;
    /* Chrome 16+, Safari 4+ */
    /* FF 5+ */
    /* Not implemented yet */
    /* IE 10+ */
    animation-fill-mode: forwards;
    /* When the spec is finished */
    background-color: transparent;
    -webkit-animation: 3s tile-flip-correct;
            animation: 3s tile-flip-correct;
    border: 1px solid grey;
}

.word-tile.present.animation {
    -webkit-animation-fill-mode: forwards;
    /* Chrome 16+, Safari 4+ */
    /* FF 5+ */
    /* Not implemented yet */
    /* IE 10+ */
    animation-fill-mode: forwards;
    /* When the spec is finished */
    background-color: transparent;
    -webkit-animation: 3s tile-flip-present;
            animation: 3s tile-flip-present;
    border: 1px solid grey;
}

.word-tile.absent.animation {
    -webkit-animation-fill-mode: forwards;
    /* Chrome 16+, Safari 4+ */
    /* FF 5+ */
    /* Not implemented yet */
    /* IE 10+ */
    animation-fill-mode: forwards;
    /* When the spec is finished */
    background-color: transparent;
    -webkit-animation: 3s tile-flip-absent;
            animation: 3s tile-flip-absent;
    border: 1px solid grey;
}

@-webkit-keyframes tile-flip-absent {
    0% {
        -webkit-transform: scale(0.9);
                transform: scale(0.9);
    }

    10% {
        -webkit-transform: scale(1.1);
                transform: scale(1.1);
        background-color: grey;
        background-color: var(--incorrect-color);
        border: none;
    }

    20% {
        -webkit-transform: scale(1);
                transform: scale(1);
        background-color: grey;
        background-color: var(--incorrect-color);
        border: none;
    }

    100% {
        -webkit-transform: scale(1);
                transform: scale(1);
        background-color: grey;
        background-color: var(--incorrect-color);
        border: none;
    }
}

