.next_blocks {
  position: absolute;
  right: -6.2vw;
  display: flex;
  width: 6vw;
  height: 14vw;
  background-color: rgb(115, 165, 122);
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  padding: 1.5vw;
  border: 0.2vw solid white;
  border-left: none;
  border-radius: 0 0.5vw 0.5vw 0;
  top: 2vw;
